@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

code {
  font-family: 'Poppins', 'Courier New', monospace;
}

* {
  box-sizing: border-box;
}


:root {
  --primary: rgba(255, 255, 255, 1);
  --secondary: rgb(70, 120, 227);
  --tertiary: rgba(120, 123, 125, 0.92);
  --light: rgba(238, 238, 247, 1);
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', "Trebuchet MS", Helvetica, sans-serif;
  color: var(--primary);
  /* background-image: url("./assets/background.jpg"); */
  line-height: 1.5;
  background-color: #bbbbbb;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.profile-picture {
  margin-left: auto;
  border-radius: 5%;
  margin-right: auto;
  display: block;
  text-align: right;
}

header {
  padding: 20px 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  top: 0;
  
}

header h1 {
  color: white;
  /* background-color: var(--secondary); */
  padding: 5px 10px 5px 10px;
  margin: 0;
  font-size: 3vw;
  line-height: 1;
}

nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style-type: none;
}

nav a {
  text-decoration: none;
  padding: 5px 10px;
  color: var(--secondary);
  background-repeat: no-repeat;
  background-size: 100% 0.2em;
  background-position: bottom;
  transition: all 0.25s ease-in;
  font-size: 1.4rem;


}

nav a:hover {
  background-size: 100% 100%;
  color: var(--primary);
}

nav a:last-child {
  margin-right: 0;
}

main {
  margin-top: 20px;
  margin-bottom: 45px;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -9999;
  background-color: var(--white);
  background-repeat: no-repeat;
  background-position: left top;
  opacity: 0.4;
}



.page-wrapper {
  max-width: 88%;
  margin-left: auto;
  margin-right: auto;
}

.page-section {
  margin: 25px 0;
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-between;
}

.page-section > h2 {
  flex: 0 0 20%;
  text-align: right;
  padding-right: 15px;
  border-right: 4px solid var(--primary);

  font-size: 3vw;
  line-height: 1.1;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.968);

}



.page-section > div {
  flex: 0 0 77%;
}

.page-section p {
  margin-bottom: 20px;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.968);
  font-size: large;
  text-align: center;
}

.grid-container {
  display: grid;
  column-gap: 10px;
  row-gap: 10px;
  grid-auto-flow: dense;
  grid-template-columns: repeat(12, 1fr);
  grid-auto-rows: minmax(150px, 1fr);
}

.grid-item {
  border: 5px solid var(--secondary);
  grid-column: span 4;
  min-height: 150px;
  display: flex;
  align-items: flex-end;
  padding: 0 0 20px 0;
  text-decoration: none;
  background-blend-mode: soft-light;
  background-size: 150%;
  transition: all 0.5s;
  font-size: 0.9rem;
}

/* feature first portfolio item */
.grid-item:first-child {
    grid-column: span 8;
    grid-row: span 2;
}

.grid-item:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

.grid-item div {
  background-color: var(--secondary);
  padding: 8px 10px;
}

.grid-item h3 {
  font-size: 1.6rem;
}

.contact address {
  display: flex ;
  flex-wrap: wrap;
  /* flex-flow: row wrap; */
  justify-content: space-around;
  align-items: center;
  height: 100%;
  padding: 200px 0px 30px 500px;
  text-align: center;
  height: 10px;
} 

.contact address a {
  color: white;
  text-decoration: none;
  font-size: 1 rem;
  font-style: normal;
  display: inline-block;
}

.contact address a:hover {
  background-size: 100% 88%;
  color: var(--secondary);
}

/* work images */
.run-buddy {
  background-image: url("./assets/hero-bg1.jpg");
}

.note-taker {
  background-image: url("./assets/notetaker.png");
}

.weather {
  background-image: url("./assets/weather.png");
}

.send-joke {
  background-image: url("./assets/Sendajoke.png");
}

.budget-tracker {
  background-image: url("./assets/budget-tracker.png");
}

.tech-blog {
  background-image: url("./assets/techblog.png");
}

/* end work images */


.email::before {
  content: url("./assets/email.svg");
}

.github::before {
  content: url("./assets/github.svg");
}

.link::before {
  content: url("./assets/link.svg");
}



.form {
  width: 60%;
  font-size: 1rem;
  padding-left: 100px;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
}

.input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

/* footer */
footer h3 {
    text-align: center;
    color: var(--tertiary);

}


/* footer end */



/* media queries */

@media screen and (max-width: 980px) {
  header {
    padding: 0 0 10px 0;
    justify-content: center;
    text-align: center;
  }

  header h1 {
    width: 100%;
    padding: 5px;
    margin-bottom: 10px;
    font-size: 10vw;
  }

  header nav {
    justify-content: center;
  }

  .page-section > h2,
  .page-section > div {
    flex: 0 0 100%;
  }

  .page-section > h2 {
    border-right: none;
    border-bottom: 4px solid var(--primary);
    margin-bottom: 20px;
    padding-bottom: 10px;
    text-align: left;
    font-size: 7vw;
  }

}

@media screen and (max-width: 768px) {
  .grid-item,
  .grid-item:first-child {
    grid-column: 1 / -1;
  }

  .grid-container {
    grid-auto-rows: minmax(200px, 1fr);
  }

  .hero-banner {
    justify-content: center;
    text-align: center;
  }

  .contact address {
    flex-direction: column;
    align-items: flex-start;
  }
}